let reviewFormToggleBtn = document.querySelectorAll("._toggle-review-form")
if(reviewFormToggleBtn){
    reviewFormToggleBtn.forEach(btn => {
        btn.onclick = function () {
            body.classList.toggle("fixed-body")
            body.classList.toggle("_open-review-form")
        }
    })
}

