let filterItems = document.querySelectorAll(".filter-item")

filterItems.forEach(filter => {

    let dropdownToggleBtn = filter.querySelector(".filter-item__toggle")
    dropdownToggleBtn.onclick = function () {
        filter.classList.add("_active")
    }

    document.addEventListener( 'click', (e) => {
        const withinBoundaries = e.composedPath().includes(filter);

        if ( ! withinBoundaries ) {
            if(filter.classList.contains("_active")){
                filter.classList.remove("_active")
            }
        }
    })

    if(!filter.classList.contains("_radios")) return
    let dropdownRadios = filter.querySelectorAll(".m-check input")
    let currentValue = filter.querySelector(".filter-item__current")

    setCurrentValue()
    dropdownRadios.forEach(radio => {
        radio.onchange = function () {
            setCurrentValue()
            filter.classList.remove("_active")
        }
    })

    function setCurrentValue() {
        let activeRadio = filter.querySelector(".m-check input:checked")
        currentValue.innerHTML = activeRadio.value
    }
})


let filterToggleBtn = document.querySelectorAll("._toggle-filter")
if(filterToggleBtn){
    filterToggleBtn.forEach(btn => {
        btn.onclick = function () {
            body.classList.toggle("fixed-body")
            body.classList.toggle("_show-filter")
        }
    })
}
